@layer components {
  .col {
    padding: 0 theme("container.padding");
    @apply flex-auto;
  }

  .col-auto {
    @apply flex-initial;
  }

  .col-1 {
    flex: 0 0 8.3333%;
    max-width: 8.3333%;
  }

  .col-2 {
    flex: 0 0 16.6666%;
    max-width: 16.6666%;
  }

  .col-3 {
    flex: 0 0 25%;
    max-width: 25%;
  }

  .col-4 {
    flex: 0 0 33.3333%;
    max-width: 33.3333%;
  }

  .col-5 {
    flex: 0 0 41.6666%;
    max-width: 41.6666%;
  }

  .col-6 {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .col-7 {
    flex: 0 0 58.3333%;
    max-width: 58.3333%;
  }

  .col-8 {
    flex: 0 0 66.6666%;
    max-width: 66.6666%;
  }

  .col-9 {
    flex: 0 0 75%;
    max-width: 75%;
  }

  .col-10 {
    flex: 0 0 83.3333%;
    max-width: 83.3333%;
  }

  .col-11 {
    flex: 0 0 91.6666%;
    max-width: 91.6666%;
  }

  .col-12 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}