}

.slider{
  position:relative;
  height:600px
}

.slider-container{
  height:100%
}

.slider-item{
  position:absolute;
  top:0px;
  left:0px;
  height:100%;
  width:100%;
  transition-property:opacity;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

.slider-btn{
  position:absolute;
  top:0px;
  display:flex;
  height:100%;
  width:8.333333%;
  align-items:center;
  justify-content:center
}

.slider-btn:hover{
  --tw-bg-opacity:1;
  background-color:rgba(var(--color-dark), var(--tw-bg-opacity));
  --tw-bg-opacity:0.1
}

.slider-btn{
  transition-property:background-color, border-color, color, fill, stroke;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

.slider-btn-prev{
  left:0px
}

.slider-btn-next{
  right:0px
}

.slider-dots{
  position:absolute;
  bottom:1rem;
  display:flex;
  width:100%;
  align-items:center;
  justify-content:center
}

.slider-dot{
  margin-left:0.375rem;
  margin-right:0.375rem;
  height:0.75rem;
  width:0.75rem;
  border-radius:9999px;
  --tw-bg-opacity:1;
  background-color:rgba(var(--color-primary), var(--tw-bg-opacity));
  --tw-bg-opacity:0.5;
  transition-property:background-color, border-color, color, fill, stroke;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

.slider-dot.active{
  --tw-bg-opacity:1
}



.modal-close-overlay{
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  --tw-bg-opacity: 1;
  background-color: rgba(var(--color-dark), var(--tw-bg-opacity));
  --tw-bg-opacity: 0.2
}

.modal{
  position: relative;
  width: 100%;
  border-radius: 0.5rem;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
