  padding-right: 2rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem
}

.modal-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom-width: 1px;
  padding-bottom: 0.75rem
}

.modal-content{
  padding-top: 1rem
